.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparentize(black,.75);
  display: none;
  &--show {
    display: block;
  }
  .body {
    background-color: #fff;
    width: 50%;
    height: 70%;
    box-shadow: 0 5px 15px rgba(0,0,0,.5);
    padding-left: 20px;
    position: relative;
    &__exit {
      position: absolute;
      right: -30px;
      top: -30px;
      color: white;
      opacity: .7;
      cursor: pointer;
      transition: opacity .2s ease;
      i {
        font-size: 30px;
      }
      &:hover {
        opacity: 1;
      }
    }
    &__title {
      color: $yellow;
      text-align: center;
    }
    &__label {
      margin-top: 25px;
      font-family: $font-semibold;
      &:after {
        content: '';
        height: 3px;
        background-color: $black;
        width: 50px;
        display: block;
        margin-top: 10px;
      }
    }
    .info {
      margin-top: 15px;
      &__label {
        font-family: $font-semibold;
      }
    }
  }
}
